body {
  padding: 0;
  height: 100%;
  margin: 0;
  background-color: #000000;
}

.App-header {
  background-color: #080808;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 8vh;
  padding: 0 10%;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
}

Input {
  border-radius: 10px;
  height: 30px;
  margin-bottom: 10px;
  border: 1px solid #ffffff;
  border-radius: 999px;
  background-color: transparent;
  text-align: center;
  color: white;
}

Input:focus {
  outline: none;
}

Select {
  padding: 0.25em;
  border: 0;
  border-bottom: 2px solid red;
  font-weight: bold;
  border-radius: 0;
  width: 150px;
  height: 30px;
}

*:focus {
  outline: none;
}

.GameCard {
  width: 174px;
  height: 225px;
  background-color: #1f1d1d;
  color: white;
  border-radius: 3%;
  margin: 5px 5px 0 0;
  transition: 0.1s ease-in-out;
  cursor: pointer;
  box-shadow: -2px -2px 30px 2px red, 2px 2px 30px 2px blue;
}

.GameCard img {
  margin-top: 20px;
  height: 140px;
  width: 165px;
  border-radius: 10px;
}

.GameCard h3 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 23px;
  line-height: 1.6rem;
}

.GameCard:hover {
  transform: scale(1.04);
  box-shadow: -2px -2px 30px 2px blue, 2px 2px 30px 2px red;
}

.loader {
  width: 10em;
  height: 10em;
  font-size: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.loader .face {
  position: absolute;
  border-radius: 50%;
  border-style: solid;
  -webkit-animation: animate 3s linear infinite;
          animation: animate 3s linear infinite;
}

.loader .face:nth-child(1) {
  width: 100%;
  height: 100%;
  color: gold;
  border-color: currentColor transparent transparent currentColor;
  border-width: 0.2em 0.2em 0em 0em;
  --deg: -45deg;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}

.loader .face:nth-child(2) {
  width: 70%;
  height: 70%;
  color: lime;
  border-color: currentColor currentColor transparent transparent;
  border-width: 0.2em 0em 0em 0.2em;
  --deg: -135deg;
  animation-direction: reverse;
}

.loader .face .circle {
  position: absolute;
  width: 50%;
  height: 0.1em;
  top: 50%;
  left: 50%;
  background-color: transparent;
  transform: rotate(var(--deg));
  transform-origin: left;
}

.loader .face .circle::before {
  position: absolute;
  top: -0.5em;
  right: -0.5em;
  content: "";
  width: 1em;
  height: 1em;
  background-color: currentColor;
  border-radius: 50%;
  box-shadow: 0 0 2em, 0 0 4em, 0 0 6em, 0 0 8em, 0 0 10em,
    0 0 0 0.5em rgba(255, 255, 0, 0.1);
}

@-webkit-keyframes animate {
  to {
    transform: rotate(1turn);
  }
}

@keyframes animate {
  to {
    transform: rotate(1turn);
  }
}

