.MainHeader {
  height: 3.2rem;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  align-items: center;
  background: #1c1c1c;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 10%;
  z-index: 5;
}

h1 {
  font-family: "Roboto", sans-serif;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

@media (min-width: 768px) {
  .MainHeader {
    justify-content: space-between;
  }
}

.NavLinks {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.NavLinks li {
  margin: 1rem;
}

.NavLinks a,
button {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
  font-size: medium;
  font-family: "Montserrat", sans-serif;
}

.NavLinks a:hover,
.NavLinks a.active {
  background: #1d1d1a;
  border-color: #292929;
  color: white;
  border-bottom: 3px solid #ff0505;
}

.NavLinks button:hover {
  background: #1d1d1a;
  border-color: #292929;
  color: white;
  border-bottom: 3px solid #ff0505;
}

@media (min-width: 768px) {
  .NavLinks {
    flex-direction: row;
  }

  .NavLinks li {
    margin: 0 0.5rem;
  }

  .NavLinks a {
    color: white;
    text-decoration: none;
  }

  .NavLinks button {
    color: white;
    background: transparent;
  }

  .NavLinks button:hover {
    background: #1f1e1c;
    color: white;
  }
}

.Avatar {
